.button-primary {
  background: transparent;
  transition: all 0.35s;
  cursor: pointer;
  padding: 0.8rem 2rem;
  transition: all 0.3 ease;
  outline: none;
  border: none;
  font-size: 10px;
  font-weight: 800;
  margin-right: 10px;
  text-transform: uppercase;
  color: white;
  border-radius: 20px;
  background: #07c7f2;
  &:hover {
    background: #03aed4;
    color: white;
  }
}
